import * as React from 'react';

// import lang from '../views.lang';

import Staff, { StaffImg, StaffImgAddButton, TSImgInstance } from '@miq/staffjs';
import { getCN } from '@miq/utiljs';
import { Button, Icons, TImg, TViewSectionProps } from '@miq/componentjs';

import { ProductImagesAddButton } from '../buttons';
import { TProductUpdateTabProps } from './utils';
import { ProductImgDeleteButton, ProductImgReorderGridItem } from './components';

export default function ProductUpdateMediaTab(props: TProductUpdateTabProps) {
  const { instance, onUpdate } = props;
  const { cover_data, images_data } = instance;

  const [isReordering, setReordering] = React.useState<boolean>(false);
  const [order, setOrder] = React.useState<TImg | null>(null);

  return (
    <Staff.Section
      title={
        <ProductImagesAddButton
          productInstance={instance}
          onSuccess={(res: any) => onUpdate?.(res.data)}
          className="d-flex align-items-center"
        >
          <Icons.CloudUpload />
          <span className="ms-1 d-none d-md-block">Upload images</span>
        </ProductImagesAddButton>
      }
      actions={
        <>
          <div className="d-none d-md-block">
            <Button
              onClick={() => setReordering(!isReordering)}
              className={getCN(['btn-round', isReordering ? 'btn-primary' : 'btn-secondary-4'])}
            >
              <Icons.DragDrop />
            </Button>
          </div>
        </>
      }
    >
      {isReordering ? (
        <div className="d-grid grid-3 gap-2">
          {images_data?.map((img) => (
            <ProductImgReorderGridItem
              {...{ img, order, setOrder }}
              onOrderChange={(data) => {
                onUpdate?.({
                  ...instance.export(),
                  images_data: images_data.map((i) => {
                    if (i.slug === data?.to?.slug) return data?.from;
                    if (i.slug === data?.from?.slug) return data?.to;
                    return i;
                  }),
                });
                setOrder(null);
              }}
              key={img.slug}
            />
          ))}
        </div>
      ) : (
        <div className="d-grid grid-2 grid-md-3 grid-lg-4 gap-2">
          <CoverCard img={StaffImg(cover_data)} productInstance={instance} {...{ onUpdate }} />

          {instance?.images_data?.map((image) => {
            const img = StaffImg(image);
            const swapCover = () => instance.swapCover(img.slug!).then(({ data }: any) => onUpdate(data));
            return (
              <ImageCard
                img={img}
                actions={<Staff.Button onClick={swapCover}>Set as cover</Staff.Button>}
                onDelete={() =>
                  onUpdate({
                    ...instance.export(),
                    images_data: instance.images_data!.filter((i) => i.slug !== img.slug),
                    images: instance.images!.filter((i) => i !== img.slug),
                  })
                }
                key={img.slug}
              />
            );
          })}
        </div>
      )}
    </Staff.Section>
  );
}

type TImgCardProps = Omit<TViewSectionProps, 'title'> & { img: TSImgInstance; onDelete: (p: TImg) => void };

const CoverCard = (
  props: Omit<TImgCardProps, 'onDelete'> & {
    productInstance: TProductUpdateTabProps['instance'];
    onUpdate: TProductUpdateTabProps['onUpdate'];
  }
) => {
  const { img, onUpdate, productInstance } = props;

  if (!img.slug)
    return (
      <div className="p-cover-add-btn d-flex align-items-center justify-content-center flex-column text-center border-1 rounded p-3">
        <p className="text-sm text-muted mb-3">Ce produit n'a pas d'image principale</p>
        <StaffImgAddButton
          onSuccess={(r) => {
            productInstance
              .update({ cover: r?.data.slug })
              .then((x) => onUpdate(x?.data))
              .catch((err) => {});
          }}
          label="Ajouter une image"
          className="btn-primary-3 btn-md"
        />
      </div>
    );

  return (
    <ImageCard
      {...props}
      onDelete={() => onUpdate({ ...productInstance.export(), cover_data: undefined, cover: undefined })}
    />
  );
};

const ImageCard = ({ img, actions, ...props }: TImgCardProps) => {
  return (
    <Staff.Section
      border
      title={
        <ProductImgDeleteButton onSuccess={(r) => props.onDelete(r?.data)} instance={img} className="btn-danger-3" />
      }
      actions={actions}
      className="p-image-card"
    >
      <StaffImg.Image {...img.export()} />
    </Staff.Section>
  );
};
