import * as React from 'react';
import { To, useNavigate } from 'react-router-dom';

import lang from '../views.lang';

import Staff from '@miq/staffjs';

import { ProductDeleteButton } from '../buttons';

import { TProductUpdateTabProps } from './utils';

const { i18n } = window;

export default function ProductUpdateSettingsTab(props: TProductUpdateTabProps<{ to: To }>) {
  const navigate = useNavigate();
  const { instance, to } = props;

  return (
    <Staff.Section title={i18n(lang, 'SettingsTab.title')}>
      <Staff.Section
        title={i18n(lang, 'SettingsTab.deleteSectionTitle')}
        actions={
          <ProductDeleteButton
            {...{ instance }}
            onSuccess={() => navigate(to)}
            onFailure={(res) => {
              if (res.status === 404) navigate('../');
            }}
          />
        }
      />
    </Staff.Section>
  );
}
