import * as React from 'react';

import { TFormCallbackProps } from '@miq/formjs';
import { Button, ConfirmButton, Icons } from '@miq/componentjs';

import { TStaffProductInstance } from '../product/models';
import type { TProductAttribute } from '../types';

export function AttributeDeleteButton({
  data,
  productInstance,
  ...props
}: { data: TProductAttribute; productInstance: TStaffProductInstance } & TFormCallbackProps) {
  if (!data?.slug || !productInstance?.slug) return null;
  const { onSuccess, onFailure, ...rest } = props;

  return (
    <ConfirmButton
      {...rest}
      renderHeader={() => <div>Supprimer un attribut</div>}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button
            onClick={() =>
              productInstance
                .deleteAttribute(data.slug)
                .then((res) => {
                  setOpen(false);
                  return onSuccess?.(res);
                })
                .catch((err) => onFailure?.(err))
            }
            className="btn-danger"
          >
            Supprimer
          </Button>
        </div>
      )}
      render={() => (
        <div className="p-3">
          Are you sure you want to delete this attribute:
          <div className="fw-bold">{data.name}</div>
        </div>
      )}
      className="attr-delete-button btn-danger-3"
    >
      <Icons.Trash />
    </ConfirmButton>
  );
}
