import * as React from 'react';

import Form, { TextFieldProps, TFormCallbackProps, useForm } from '@miq/formjs';

import type { TProductAttribute } from '../types';
import { StaffProduct } from '../product/models';

import { AttributeDeleteButton } from './buttons';

type TAttrFormProps = TFormCallbackProps & {
  productInstance: ReturnType<typeof StaffProduct>;
};

export const AttributeUpdateForm = ({
  data,
  productInstance,
  ...props
}: TAttrFormProps & { data: TProductAttribute }) => {
  const form = useForm<TProductAttribute>({ name: data.name || '', value: data.value || '' });

  if (!data?.slug || !productInstance?.slug) return null;
  const { onSuccess, onFailure } = props;

  return (
    <Form
      context={form}
      onSubmit={(e) => {
        e.preventDefault();

        productInstance
          .patchAttribute(data.slug, form.values)
          .then((res) => onSuccess?.(res))
          .catch((err) => {
            form.handleError(err);
            return onFailure?.(err);
          });
      }}
    >
      <div className="d-grid grid-md-6 gap-1">
        <div className="span-md-2">
          <AttrNameField error={form.errors.name} />
        </div>

        <div className="span-md-3">
          <AttrValueField error={form.errors.value} className="" />
        </div>

        <div className="span-md-1">
          <Form.Submit value="Save" className="btn-primary-2 me-2" />
          <AttributeDeleteButton {...{ productInstance, data, onSuccess, onFailure }} />
        </div>
      </div>
    </Form>
  );
};

const formDefaultValues = { name: '', value: '' };

export const AttributeCreateForm = ({ productInstance, ...props }: TAttrFormProps) => {
  const form = useForm(formDefaultValues);

  if (!productInstance?.slug) return null;

  return (
    <Form
      context={form}
      onSubmit={(e) => {
        e.preventDefault();

        productInstance
          .postAttribute(form.values)
          .then((res) => {
            form.setValues(formDefaultValues);
            return props?.onSuccess?.(res);
          })
          .catch((err) => {
            form.handleError(err);
            return props?.onFailure?.(err);
          });
      }}
    >
      <div className="d-grid grid-md-6 gap-1">
        <div className="span-md-2">
          <AttrNameField error={form.errors.name} />
        </div>

        <div className="span-md-3">
          <AttrValueField error={form.errors.value} className="" />
        </div>

        <div className="span-md-1">
          <Form.Submit value="Save" className="btn-primary-2" />
        </div>
      </div>
    </Form>
  );
};

// =============================================================================
//                   FIELDS
// =============================================================================

const AttrNameField = (props: TextFieldProps) => {
  const { label, text, error, placeholder = `Entrez le nom de l'attribut ...`, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Text placeholder={placeholder} maxLength={30} minLength={2} {...rest} required name="name" />
    </Form.Field>
  );
};

const AttrValueField = (props: TextFieldProps) => {
  const { label, text, error, placeholder = `Entrez la valuer de l'attribut ...`, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Text placeholder={placeholder} maxLength={99} minLength={2} {...rest} required name="value" />
    </Form.Field>
  );
};
