import * as React from 'react';
import { useParams, useNavigate, useSearchParams } from 'react-router-dom';

import './views.update.scss';
import lang from '../views.lang';

import Staff from '@miq/staffjs';
import { useForm } from '@miq/formjs';
import { useRequest, Response } from '@miq/hookjs';
import { Loading, QueryTabs } from '@miq/componentjs';
import { addForwardSlash, TLang } from '@miq/utiljs';

import { emitter } from '../../events';
import { StaffProduct } from '../models';
import { PublishButton, UnPublishButton } from '../../components';

import { ProductCardItem } from '../components';

import ProductUpdateInfoTab from './tabs.InfoUpdate';
import ProductUpdateMediaTab from './tabs.MediaUpdate';
import ProductUpdateInventoryTab from './tabs.InventoryUpdate';
import ProductUpdateSettingsTab from './tabs.SettingsUpdate';
import { SharedDataCtx } from '@miq/contextjs';

const i18n = (k: keyof TLang) => window.i18n(lang, k);

//#region FIELDS

const formInfoFields = [
  'name',
  'category',
  'description',
  'retail_price',
  'is_pre_sale',
  'is_pre_sale_text',
  'is_pre_sale_dt',
  'is_oos',
  'is_on_sale',
  'sale_price',
  'is_explicit',
];
const configFormFields = ['cost', 'color_group_pk', 'stage'];
const supplierFormFields = [
  'supplier_name',
  'supplier_item_id',
  'supplier_item_sn',
  'supplier_item_category',
  'supplier_item_url',
  'supplier_item_cost',
  'supplier_item_cost_currency',
  'supplier_order_id',
];
const seoFields = ['meta_title', 'meta_description', 'meta_slug'];
const formFields = [...formInfoFields, ...configFormFields, ...supplierFormFields, ...seoFields];
const formDefaultValues: any = {
  retail_price: 0.0,
  is_pre_sale: false,
  sale_price: 0.0,
  is_on_sale: false,
  is_published: false,
};
const mapData2Values = (data: any = {}) => {
  const values: any = {};
  formFields.forEach((f) => {
    const value = data?.[f];
    if (value == null) {
      values[f] = formDefaultValues[f] ?? '';
      return;
    }
    values[f] = value;
  });
  return { ...formDefaultValues, ...values };
};

//#endregion FIELDS

//#region ProductStaffUpdateView

export default function ProductStaffUpdateView(props: any) {
  let { back } = props;
  const { prodSlug } = useParams();
  const [params] = useSearchParams();
  const navigate = useNavigate();

  const ctx = React.useContext(SharedDataCtx);
  const { product, categories } = ctx;

  const form = useForm(mapData2Values(product));
  // const { res } = useRequest(() => StaffProduct({ slug: prodSlug }).retrieve(), {
  //   refreshDeps: [prodSlug],
  //   onSuccess: ({ data }) => {
  //     document.title = `${data?.name} - Update product`;
  //     // form.setValues(mapData2Values(data));
  //   },
  //   onError: ({ response }) => {
  //     if (response?.status === 404) return navigate('../');
  //   },
  // });

  // const r = new Response(res);
  // if (!r.isSuccess) return null;

  // if (loading) return <Loading />;

  const instance = StaffProduct(product);

  const updateData = (d: any) => {
    ctx.updateData({ ...ctx, product: { ...product, ...d } });
    // request.updateData(d);
    emitter.update(d);
  };

  return (
    <Staff.View
      className="product-staff-update-view"
      requiredPerms={['store.change_product']}
      title={<ProductCardItem data={{ ...instance.export() }} />}
      back={`${addForwardSlash(back)}${`${params}` ? `?${params}` : ''}`}
      actions={
        instance.is_published ? (
          <UnPublishButton instance={instance} onSuccess={({ data }) => updateData(data)}>
            Dépublier
          </UnPublishButton>
        ) : (
          <PublishButton
            instance={instance}
            onSuccess={({ data }) => updateData(data)}
            onFailure={(err) => form.handleError(err)}
          >
            Publier
          </PublishButton>
        )
      }
    >
      <Staff.Section title={<div className="mb-1">{QButtons}</div>}>
        <QueryTabs>
          <QueryTabs.Tab index>
            <ProductUpdateInfoTab {...{ form, instance }} categories={categories} onUpdate={updateData} />
          </QueryTabs.Tab>

          <QueryTabs.Tab value="media">
            <ProductUpdateMediaTab {...{ instance }} onUpdate={updateData} />
          </QueryTabs.Tab>

          <QueryTabs.Tab value="inventory">
            <ProductUpdateInventoryTab {...{ instance }} onUpdate={updateData} />
          </QueryTabs.Tab>

          <QueryTabs.Tab value="settings">
            <ProductUpdateSettingsTab {...{ instance }} onUpdate={updateData} to={back} />
          </QueryTabs.Tab>
        </QueryTabs>
      </Staff.Section>
    </Staff.View>
  );
}

//#endregion ProductStaffUpdateView

const QButtons = (
  <div className="query-buttons">
    <QueryTabs.Button index target="__tab" className="btn-md">
      Info
    </QueryTabs.Button>
    <QueryTabs.Button target="__tab" value="media" className="btn-md">
      {i18n('MediaTab.title')}
    </QueryTabs.Button>
    <QueryTabs.Button target="__tab" value="inventory" className="btn-md">
      {i18n('InventoryTab.title')}
    </QueryTabs.Button>
    <QueryTabs.Button target="__tab" value="settings" className="btn-md">
      {i18n('SettingsTab.title')}
    </QueryTabs.Button>
  </div>
);
