import * as React from 'react';
import { Icons, TImg } from '@miq/componentjs';

import { StaffImg, StaffImgDeleteButton, TImgButtonDeleteProps } from '@miq/staffjs';

export const ProductImgReorderGridItem = ({
  img,
  ...props
}: {
  img: TImg;
  order: TImg | null;
  setOrder: React.Dispatch<React.SetStateAction<TImg | null>>;
  onOrderChange: (p: { from: TImg; to: TImg }) => void;
}) => {
  const { order, setOrder, onOrderChange } = props;

  return (
    <div
      draggable
      onDragEnter={(e) => setOrder(img)}
      onDragEnd={(e) => {
        if (!order || order.slug === img.slug) return;

        StaffImg(img)
          .swapPosition(order.slug!, order.position)
          .then(({ data }) => onOrderChange(data))
          .catch((err) => {
            console.error(err);
          });
      }}
    >
      <StaffImg.Square {...img} className="rounded" />
    </div>
  );
};

export const ProductImgDeleteButton = (props: TImgButtonDeleteProps) => (
  <StaffImgDeleteButton {...props}>
    <Icons.Trash />
  </StaffImgDeleteButton>
);
