import * as React from 'react';

import lang from '../views.lang';

import Staff from '@miq/staffjs';
import { Button } from '@miq/componentjs';

import { SizeAddForm, SizeUpdateForm } from '../../productSize';
import { AttributeCreateForm, AttributeUpdateForm } from '../../productAttribute';

import { TProductUpdateTabProps } from './utils';

const { i18n } = window;

const SizeSection = (props: TProductUpdateTabProps) => {
  const [addSize, setAddSize] = React.useState(false);
  const { instance, onUpdate } = props;

  return (
    <Staff.Section
      title="Tailles"
      text="Ajouter des tailles"
      actions={
        <Button onClick={() => setAddSize(!addSize)} className="btn-primary-3">
          Ajouter une taille
        </Button>
      }
    >
      {addSize && (
        <Staff.Section text="Ajouter une taille">
          <SizeAddForm productInstance={instance} onSuccess={({ data }) => onUpdate(data)} />
        </Staff.Section>
      )}

      {instance.sizes?.map((size) => {
        return (
          <SizeUpdateForm
            data={size}
            productInstance={instance}
            onSuccess={({ data }) => onUpdate(data)}
            key={size.slug}
          />
        );
      })}
    </Staff.Section>
  );
};

const AttributeSection = (props: TProductUpdateTabProps) => {
  const { instance, onUpdate } = props;
  const [addAtr, setAddAtr] = React.useState(false);

  return (
    <Staff.Section
      title="Attributs"
      actions={
        <Button onClick={() => setAddAtr(!addAtr)} className="btn-primary-3">
          Ajouter un attribut
        </Button>
      }
    >
      {addAtr && (
        <Staff.Section text="Ajouter un attribut au produit.">
          <AttributeCreateForm productInstance={instance} onSuccess={({ data }) => onUpdate(data)} />
        </Staff.Section>
      )}

      {instance?.attributes?.map((attr) => (
        <div className="mb-1" key={attr.slug}>
          <AttributeUpdateForm data={attr} productInstance={instance} onSuccess={({ data }) => onUpdate(data)} />
        </div>
      ))}
    </Staff.Section>
  );
};

export default function ProductUpdateInventoryTab(props: TProductUpdateTabProps) {
  return (
    <Staff.Section title={i18n(lang, 'InventoryTab.title')}>
      <SizeSection {...props} />
      <AttributeSection {...props} />
    </Staff.Section>
  );
}
